.popup {
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  position: fixed;
  width: 100%;
  min-height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s ease-in-out;
  display: flex;
  box-sizing: border-box;
  overflow: hidden;
}
