.profile__discription {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  box-sizing: border-box;
}


@media screen and (max-width: 720px) {
  .profile__discription {
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    max-width: 282px;
  }
}
